<template>
  <el-row class="form-bg">
    <el-col :span="10" :offset="6">
      <el-form ref="form" :rules="rules" :model="forminfo" label-width="100px">
        <el-form-item label="菜单类型">
          <el-radio v-model="forminfo.type" :label="1">目录</el-radio>
          <el-radio v-model="forminfo.type" :label="2">菜单</el-radio>
        </el-form-item>
        <el-form-item :label="forminfo.type==1 ? '目录名称':'菜单名称'" prop="title">
          <el-input v-model="forminfo.title"></el-input>
        </el-form-item>
        <!-- 目录 -->
        <template v-if="forminfo.type==1" >
          <el-form-item label="目录图标" v-if="forminfo.type==1" prop='icon'>
            <el-input v-model="forminfo.icon"></el-input>
          </el-form-item>
        </template>
        <!-- 菜单 -->
        <template v-if="forminfo.type==2">
          <el-form-item label="上级目录"  prop="pid">
            <el-select v-model="forminfo.pid" placeholder="请选择">
              <el-option label="顶级目录" :value="0"></el-option>
              <el-option
                v-for="item in menulist"
                :key="item.id"
                :label="item.title"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="菜单地址" v-if="forminfo.type==2" prop="url">
            <el-input v-model="forminfo.url"></el-input>
          </el-form-item>
        </template>

        <el-form-item label="状态">
          <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import { addMenu, getMenuList, editMenu } from "@/request/menu";
let defaultItem;
export default {
  data() {
    return {
      forminfo: {
        pid: 0,
        title: "",
        icon: "",
        type: 1, // 菜单类型   1目录  2菜单
        url: "",
        status: 1, // 状态，1是正常，2是禁用
      },
      menulist: [],
      rules:{
          title: [{ required: true, message: '请输入名称', trigger: 'blur' }],
          icon:[{ required: true, message: '请输入图标', trigger: 'blur' }],
          pid:[{ required: true, message: '请选择上级目录', trigger: 'blur' }],
          url:[{ required: true, message: '请输入地址', trigger: 'blur' }],
      }
    };
  },
  created() {
    this.getlist();
    // 如果是修改页面，就请求当前页面的数据！
    if (this.$route.params.id) {
      this.getinfo();
    }
  },
  methods: {
    async getlist() {
      this.menulist = await getMenuList();
      console.log(this.menulist);
    },
    async getinfo() {
      this.forminfo = await getMenuList(this.$route.params.id);
      defaultItem = Object.assign({},this.forminfo);
    },
    submit() {
      // 验证
      // this.$refs.form.validate(valid=>{ valid 为true验证通过，false表示验证失败  })
      this.$refs.form.validate(valid=>{
        if(valid){
          // 提交数据
          if (this.$route.params.id) {
            editMenu(this.$route.params.id, this.forminfo);
          } else {
            addMenu(this.forminfo);
          }
        }else{
            this.$message.warning('验证失败')
        }
      })
    },
    reset(){
      if (this.$route.params.id) {
         this.forminfo =  Object.assign({},defaultItem);; 
      }else{
        this.$refs.form.resetFields();
      }
       
    }
  },
  components: {},
};

// BootStrap
</script>
<style scoped>
</style>


